<template>
  <div class="xmatrix-page icons-container">
    <h3>Svg Icon</h3>
    <div class="icon-group" v-for="(iconsMap, key) of iconsMapGroups" :key="key">
      <hr>
      <h3 style="text-align: center">{{key}}</h3>
      <hr>
      <div class="icon-wrapper" v-for="item of iconsMap" :key="item"
           @click="handleClipboard(generateIconCode(item),$event)">
        <el-tooltip placement="top">
          <div slot="content">
            {{generateIconCode(item)}}
          </div>
          <div class="icon-item">
            <div class="svg-wrapper">
              <svg-icon class-name="disabled" :icon-class="item" />
            </div>
            <span>{{item}}</span>
          </div>
        </el-tooltip>
      </div>
    </div>
  </div>
</template>

<script>
// 需在webpack.base.conf.js中配置svgloader
// 需在main.js中引入 import '@/components/svg-icon' // svg-icon
// 需在components中设置
// import _ from 'lodash'
import icons from './generateIconsView'
import clipboard from '@/utils/clipboard'
export default {
  name: 'svg-icon-demo',
  created () {
  },
  data () {
    return {
      iconsMapGroups: {}
    }
  },
  mounted () {
    const iconsMapGroups = {}
    // console.log('icons.state.iconsMapGroups', icons.state.iconsMapGroups)
    for (let key in icons.state.iconsMapGroups) {
      const iconsMap = icons.state.iconsMapGroups[key].map((i) => {
        const iconName = i.default.id.replace(/^icon-(.*)$/, '$1')
        return iconName
      })
      iconsMapGroups[key] = iconsMap
    }
    this.iconsMapGroups = iconsMapGroups
  },
  methods: {
    generateIconCode (symbol) {
      return `<svg-icon icon-class="${symbol}" />`
    },
    handleClipboard (text, event) {
      clipboard(text, event)
    }
  },
  computed: {
  }
}
</script>
<style lang="scss" scoped>
  .icons-container {
    .icon-wrapper {
      display: inline-block;
    }
    .icon-item {
      width: 120px;
      height: 60px;
      font-size: 30px;
      color: #24292e;
      cursor: pointer;
      display:flex;
      flex-direction: column;
      .svg-wrapper {
        flex: 1;
        text-align: center;
      }
      span {
        flex:1;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        font-size: 13px;
        text-align:center;
      }
    }
    .disabled{
      pointer-events: none;
    }
    [class^="el-icon"], [class*=" el-icon"] {
      padding-left: 5px;
      padding-top: 5px;
      font-size: 30px !important;
    }
  }
</style>
